@import url(https://fonts.googleapis.com/css2?family=Piazzolla:ital,wght@1,300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header{
	border-bottom-width:1px;
	box-shadow: 0px 5px  #ebebe0;
	padding: 10px; 
	padding-left: 150px;
	padding-top: 15px;
}
.logo{
	font-weight: bold;
	display: inline;
	font-size: 25px;
	color: #0099ff;
}
.light-weight-font{
	font-weight: lighter;
	color: #0099ff;
}
.section{
	width: -webkit-fit-content;
	width: -moz-fit-content;
	width: fit-content;
	float: left;
	padding-top: 30px;
	padding-left: 200px;
	font-weight: 100 !important;
	font-size: 35px;
	font-family: 'Piazzolla', serif;
	color: #008fb3;
	margin-bottom: 0px !important;
	padding-bottom: 0px !important;
}
small{
	float: left;
	padding-left:0px;
	font-size: 20px;
	font-weight: lighter;
	color:#293d3d;
	padding-top: 10px;
}
h2,h5{
	text-decoration: none;
}
.intersets{
	overflow-wrap: break-word;
	overflow: anywhere;
	width: 250px;
	padding-top: 0px !important;
	margin-top: 0px !important;
}
.transition-slow-appear{
	margin-top: 0px !important;
	-webkit-animation: slow-appear 0.2s;
	        animation: slow-appear 0.2s;
}
@-webkit-keyframes slow-appear{
	0%{width: 20px;}
	25%{width: 30px;}
	50%{width:40px;}
	75%{width: 50px;}
	100%{width: -webkit-fit-content;width: fit-content;}
}
@keyframes slow-appear{
	0%{width: 20px;}
	25%{width: 30px;}
	50%{width:40px;}
	75%{width: 50px;}
	100%{width: -webkit-fit-content;width: -moz-fit-content;width: fit-content;}
}
.circle-area{
  float: right;
  border-radius: 800px 800px 800px 800px;
  border-style: circle;
  border:1px solid lightgray;
  width: 800px;
  height: 700px;
  padding-top: 20px;
  margin-top: 30px;
  padding-right: 0px;
  border-bottom:0px dotted;
  border-right:0px;
  box-sizing: border-box;
  display: flex;
  position: relative;
  align-items: center;
  justify-content: center;
  overflow-wrap: anywhere;
  shape-outside: content-box;
  overflow: auto;
  background-color: #2D3232;
}
.btn-circle-medium{
 border-radius: 50%;
 height: 80px;
 width: 80px;
 padding:5px;
 background-color: white;
 border:1px dotted rgba(0,0,0,0.5);
 outline: none;
 position: absolute;
 -webkit-hyphens: auto;
     -ms-hyphens: auto;
         hyphens: auto;
 display: flex;
 justify-content: center;
 align-items: center;
 overflow-wrap: anywhere !important;
 text-align: center;
 margin: 10px !important;
 background-color: #10DCF1;
 transition: font-size 0.5s;
}
.btn-circle-medium:hover{
   -webkit-animation: zoom 0.8s;
           animation: zoom 0.8s;
  cursor: pointer;
  outline: none;
  color:   #4d4d00;
}
@-webkit-keyframes zoom{
  0%{font-size:18px;}
  50%{font-size: 20px;}
  100%px{font-size: 18px;}
}
@keyframes zoom{
  0%{font-size:18px;}
  50%{font-size: 20px;}
  100%px{font-size: 18px;}
}
button{
  text-decoration: none !important;
}
