.circle-area{
  float: right;
  border-radius: 800px 800px 800px 800px;
  border-style: circle;
  border:1px solid lightgray;
  width: 800px;
  height: 700px;
  padding-top: 20px;
  margin-top: 30px;
  padding-right: 0px;
  border-bottom:0px dotted;
  border-right:0px;
  box-sizing: border-box;
  display: flex;
  position: relative;
  align-items: center;
  justify-content: center;
  overflow-wrap: anywhere;
  shape-outside: content-box;
  overflow: auto;
  background-color: #2D3232;
}
.btn-circle-medium{
 border-radius: 50%;
 height: 80px;
 width: 80px;
 padding:5px;
 background-color: white;
 border:1px dotted rgba(0,0,0,0.5);
 outline: none;
 position: absolute;
 hyphens: auto;
 display: flex;
 justify-content: center;
 align-items: center;
 overflow-wrap: anywhere !important;
 text-align: center;
 margin: 10px !important;
 background-color: #10DCF1;
 transition: font-size 0.5s;
}
.btn-circle-medium:hover{
   animation: zoom 0.8s;
  cursor: pointer;
  outline: none;
  color:   #4d4d00;
}
@keyframes zoom{
  0%{font-size:18px;}
  50%{font-size: 20px;}
  100%px{font-size: 18px;}
}
button{
  text-decoration: none !important;
}