@import url('https://fonts.googleapis.com/css2?family=Piazzolla:ital,wght@1,300&display=swap');

.header{
	border-bottom-width:1px;
	box-shadow: 0px 5px  #ebebe0;
	padding: 10px; 
	padding-left: 150px;
	padding-top: 15px;
}
.logo{
	font-weight: bold;
	display: inline;
	font-size: 25px;
	color: #0099ff;
}
.light-weight-font{
	font-weight: lighter;
	color: #0099ff;
}
.section{
	width: fit-content;
	float: left;
	padding-top: 30px;
	padding-left: 200px;
	font-weight: 100 !important;
	font-size: 35px;
	font-family: 'Piazzolla', serif;
	color: #008fb3;
	margin-bottom: 0px !important;
	padding-bottom: 0px !important;
}
small{
	float: left;
	padding-left:0px;
	font-size: 20px;
	font-weight: lighter;
	color:#293d3d;
	padding-top: 10px;
}
h2,h5{
	text-decoration: none;
}
.intersets{
	overflow-wrap: break-word;
	overflow: anywhere;
	width: 250px;
	padding-top: 0px !important;
	margin-top: 0px !important;
}
.transition-slow-appear{
	margin-top: 0px !important;
	animation: slow-appear 0.2s;
}
@keyframes slow-appear{
	0%{width: 20px;}
	25%{width: 30px;}
	50%{width:40px;}
	75%{width: 50px;}
	100%{width: fit-content;}
}